import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

function toFixedWithoutRounding(number: number, decimalPlaces: number): number | string {
  const multiplier = Math.pow(10, decimalPlaces);
  let ans = ('' + (Math.floor(number * multiplier) / multiplier))
  return ans.split('.').length === 1 ? ans + ".0" : ans;
}

@Pipe({
  name: 'numberShort',
  standalone: true
})
export class NumberShort implements PipeTransform {
  transform(num: number | string): string | number {
    if (num === undefined || num === null) {
      return ""
    }
    if (typeof (num) == "string") {
      num = parseFloat(num.replace(/,/g, ''));
    }
    if (num >= 1000000000) {
      // return (num/1000000000).toFixed(2) + 'B';
      return toFixedWithoutRounding(num / 1000000000, 1) + 'B';
    } else if (num >= 1000000 && num < 1000000000) {
      // return (num/1000000).toFixed(1) + 'M';
      return toFixedWithoutRounding(num / 1000000, 1) + 'M';
    }
    // else if (num >= 1000 && num < 1000000) {
    //   // return (num/1000).toFixed(2) + 'K';
    //   return toFixedWithoutRounding(num/1000, 2) + 'K';
    // } 
    else {
      return num.toLocaleString("en-US", { minimumFractionDigits: 2, maximumFractionDigits: 2 });
    }
  }
}